import { ArrowLeftOutlined, CheckOutlined, LoadingOutlined } from '@ant-design/icons';
import { CustomInput, CustomMobileInput } from 'components/formik';
import { ACCOUNT } from 'constants/routes';
import { Field, useFormikContext, FieldArray, ErrorMessage } from 'formik';
import PropType from 'prop-types';
import React from 'react';
import { useHistory } from 'react-router-dom';
import { SocialIcon } from 'react-social-icons';

const EditForm = ({ isLoading, authProvider }) => {
  const history = useHistory();
  const { values, submitForm } = useFormikContext();

  return (
    <div className="user-profile-details">
      <Field
        disabled={isLoading}
        name="fullname"
        type="text"
        label="* Full Name"
        placeholder="Enter your full name"
        component={CustomInput}
        style={{ textTransform: 'capitalize' }}
      />


      <Field
        disabled={authProvider !== 'password' || isLoading}
        name="email"
        type="email"
        label="* Email Address"
        placeholder="test@example.com"
        component={CustomInput}
      />


<Field
        disabled={isLoading}
        name="job"
        type="text"
        label="* Job Title"
        placeholder="Accounting Manager"
        component={CustomInput}
      />

<Field
        disabled={isLoading}
        name="website"
        type="text"
        label="Website"
        placeholder="https://www.YourWebsite.com"
        component={CustomInput}
      />
<Field
        disabled={isLoading}
        name="bio"
        type="text"
        label="BIO"
        placeholder="All your dreams can come true and I'll make sure of it ."
        component={CustomInput}
      />

      <Field
        disabled={isLoading}
        name="address"
        type="text"
        label="Address"
        placeholder="3rd District, 6 Oct, Giza, Egypt"
        component={CustomInput}
        style={{ textTransform: 'capitalize' }}
      />
      <Field
        disabled={isLoading}
        name="gMaps"
        type="text"
        label="Location URL (Google Maps)"
        placeholder="https://goo.gl/maps/@Your_Google_Coordiantes"
        component={CustomInput}
      />

      {/*START HERE */}




      <FieldArray name="friends">
        {({ insert, remove, push }) => (

          <div>
            <h4 className="text-subtle">Social Accounts</h4>

            <button
              type="button"
              className="button button-small"
              onClick={() => insert(0,{ name: '', email: '' })}
            >
              Add New Account
            </button>
            <br />
            {values.friends.length > 0 &&
              values.friends.map((friend, index) => (
                <div key={index}>
                  <div style={{ backgroundColor: '#f5f5f5', padding: '5px', borderRadius:'25px' }}>

                    {/* <label htmlFor={`friends.${index}.name`}>Name</label> */}
                    <div className="d-flex-center">

                    <SocialIcon url={friend.name} target='_blank' />
</div>
                    <Field
                      label={`Social Media Account # ${index + 1}`}
                      disabled={isLoading}

                      name={`friends.${index}.name`}
                      placeholder="https://www.socialprovider.com/Your_Profile_Name"
                      type="text"
                      component={CustomInput}

                    />
                    <br />
                    <button
                      type="button"
                      className="button button-small"
                      onClick={() => remove(index)}
                    >
                      Delete
                    </button>
                    {/* <label htmlFor={`friends.${index}.email`}>Email</label> */}

                  </div>
                  <br />
                </div>

              ))}

          </div>
        )}
      </FieldArray>
      {/*START HERE */}

      <Field
        disabled={isLoading}
        name="whatsapp"
        type="text"
        label="WhatsApp Number"
        placeholder="01012345678"
        component={CustomInput}
      />

      <CustomMobileInput
        defaultValue={values.mobile}
        name="mobile"
        disabled={isLoading}
        label="Personal Mobile Number (Will be used for checkout)"
      />

      <br />
      <CustomMobileInput
        defaultValue={values.bisMobile}
        name="bisMobile"
        disabled={isLoading}
        label="Business Mobile Number"
      />

      <br />
      <div className="edit-user-action">
        <button
          className="button button-muted w-100-mobile"
          disabled={isLoading}
          onClick={() => history.push(ACCOUNT)}
          type="button"
        >
          <ArrowLeftOutlined />
          &nbsp;
          Back to Profile
        </button>
        <button
          className="button w-100-mobile"
          disabled={isLoading}
          onClick={submitForm}
          type="button"
        >
          {isLoading ? <LoadingOutlined /> : <CheckOutlined />}
          &nbsp;
          {isLoading ? 'Updating Profile' : 'Update Profile'}
        </button>
      </div>
    </div>
  );
};

EditForm.propTypes = {
  isLoading: PropType.bool.isRequired,
  authProvider: PropType.string.isRequired
};

export default EditForm;
